import { collection, getDocs, doc, updateDoc, query, where } from 'firebase/firestore';
import { db } from '../firebase';
import { UserWithSubscription, isSubscriptionValid } from './subscriptionService';
import { FreeTrialService } from './freeTrialService';

interface SubscriptionUpdateResult {
  updated: number;
  errors: number;
  details: {
    userId: string;
    email: string;
    previousStatus: boolean;
    newStatus: boolean;
    error?: string;
  }[];
}

/**
 *     
 */
const notifySubscriptionUpdate = (userId: string, isActive: boolean) => {
  console.log(`      ${userId}: ${isActive ? '' : ''}`);
  
  //     
  const event = new CustomEvent('userSubscriptionUpdated', {
    detail: { userId, isActive, timestamp: Date.now() }
  });
  window.dispatchEvent(event);
  
  //    
  const userEvent = new CustomEvent(`subscriptionUpdated_${userId}`, {
    detail: { isActive, timestamp: Date.now() }
  });
  window.dispatchEvent(userEvent);
};

/**
 *      
 */
export const updateExpiredSubscriptions = async (): Promise<SubscriptionUpdateResult> => {
  const result: SubscriptionUpdateResult = {
    updated: 0,
    errors: 0,
    details: []
  };

  try {
    console.log('       ...');
    
    // 1.    
    const usersQuery = query(
      collection(db, 'users'),
      where('subscription.isActive', '==', true)
    );
    
    const usersSnapshot = await getDocs(usersQuery);
    const users = usersSnapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    })) as (UserWithSubscription & { id: string })[];

    console.log(`    ${users.length}    `);

    //         
    for (const user of users) {
      try {
        //      
        const isValid = isSubscriptionValid(user.subscription);
        
        if (!isValid && user.subscription?.isActive) {
          //        -   
          await updateDoc(doc(db, 'users', user.id), {
            'subscription.isActive': false,
            'subscription.deactivatedAt': new Date(),
            'subscription.deactivatedBy': 'system-auto-update',
            'subscription.deactivationReason': '   ',
            updatedAt: new Date()
          });

          //     
          notifySubscriptionUpdate(user.id, false);

          result.updated++;
          result.details.push({
            userId: user.id,
            email: user.email || ' ',
            previousStatus: true,
            newStatus: false
          });

          console.log(`     : ${user.email}`);
        }
      } catch (error) {
        result.errors++;
        result.details.push({
          userId: user.id,
          email: user.email || ' ',
          previousStatus: user.subscription?.isActive || false,
          newStatus: user.subscription?.isActive || false,
          error: error instanceof Error ? error.message : '  '
        });

        console.error(`      ${user.email}:`, error);
      }
    }

    // 2.     ( )
    const trialsQuery = query(
      collection(db, 'users'),
      where('isOnFreeTrial', '==', true)
    );

    const trialsSnapshot = await getDocs(trialsQuery);
    const trialUsers = trialsSnapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data()
    })) as any[];

    console.log(`    ${trialUsers.length}     `);

    for (const user of trialUsers) {
      try {
        //           
        const trialStatus = await FreeTrialService.checkTrialValidity(user.id);
        
        if (trialStatus.isExpired) {
          //     checkTrialValidity
          //      
          notifySubscriptionUpdate(user.id, false);
          
          result.updated++;
          result.details.push({
            userId: user.id,
            email: user.email || ' ',
            previousStatus: true,
            newStatus: false
          });
          
          console.log(`     : ${user.email}`);
        }
      } catch (error) {
        result.errors++;
        console.error(`      ${user.email}:`, error);
      }
    }

    console.log(`   : ${result.updated}  ${result.errors} `);
    return result;

  } catch (error) {
    console.error('     :', error);
    throw error;
  }
};

/**
 *     
 */
export class SubscriptionAutoUpdater {
  private intervalId: NodeJS.Timeout | null = null;
  private isRunning = false;
  private updateInterval = 60 * 60 * 1000; //  

  constructor(intervalMinutes: number = 60) {
    this.updateInterval = intervalMinutes * 60 * 1000;
  }

  /**
   *   
   */
  start(): void {
    if (this.isRunning) {
      console.log('    ');
      return;
    }

    console.log(`      ${this.updateInterval / 60000} `);
    
    //   
    this.runUpdate();
    
    //    
    this.intervalId = setInterval(() => {
      this.runUpdate();
    }, this.updateInterval);
    
    this.isRunning = true;
  }

  /**
   *   
   */
  stop(): void {
    if (this.intervalId) {
      clearInterval(this.intervalId);
      this.intervalId = null;
    }
    this.isRunning = false;
    console.log('     ');
  }

  /**
   *    
   */
  private async runUpdate(): Promise<void> {
    try {
      const result = await updateExpiredSubscriptions();
      
      if (result.updated > 0) {
        console.log(`   ${result.updated}   `);
        
        //     
        this.notifyAdmin(result);
      }
    } catch (error) {
      console.error('    :', error);
    }
  }

  /**
   *    
   */
  private notifyAdmin(result: SubscriptionUpdateResult): void {
    //       
    console.log('    :', {
      updated: result.updated,
      errors: result.errors,
      timestamp: new Date().toISOString()
    });
  }

  /**
   *     
   */
  getStatus(): { isRunning: boolean; intervalMinutes: number } {
    return {
      isRunning: this.isRunning,
      intervalMinutes: this.updateInterval / 60000
    };
  }

  /**
   *   
   */
  setInterval(minutes: number): void {
    this.updateInterval = minutes * 60 * 1000;
    
    if (this.isRunning) {
      this.stop();
      this.start();
    }
    
    console.log(`      ${minutes} `);
  }
}

//     
export const subscriptionAutoUpdater = new SubscriptionAutoUpdater(60); //  

/**
 *   
 */
export const initializeAutoUpdater = (): void => {
  //     
  const autoUpdateEnabled = localStorage.getItem('subscription_auto_update') !== 'false';
  
  if (autoUpdateEnabled) {
    subscriptionAutoUpdater.start();
    console.log('     ');
  } else {
    console.log('    ');
  }
};

/**
 * /   
 */
export const toggleAutoUpdater = (enabled: boolean): void => {
  localStorage.setItem('subscription_auto_update', enabled.toString());
  
  if (enabled) {
    subscriptionAutoUpdater.start();
  } else {
    subscriptionAutoUpdater.stop();
  }
  
  console.log(`${enabled ? '  ' : '   '}   `);
};